@use "../../scss/" as *;

.watch{
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    .back{
        display: flex;
        align-items: center;
        position: absolute;
        cursor: pointer;
        z-index: 2;
        top: 20px;
        right: 30px;
        color: $white;
        font-size: 1.5em;
        font-weight: 700;
    }

    .movie{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}